body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans), Arial, Helvetica, sans-serif;
}
@keyframes accordion-down {
  from {
    height: 0;
  }
  to {
    height: var(--radix-accordion-content-height);
  }
}
@keyframes accordion-up {
  from {
    height: var(--radix-accordion-content-height);
  }
  to {
    height: 0;
  }
}
.container {
  margin-inline: auto;
  padding-inline: calc(var(--spacing) * 4);
  @media (width >= 40rem) {
    padding-inline: calc(var(--spacing) * 6);
  }
  @media (width >= 64rem) {
    padding-inline: calc(var(--spacing) * 8);
  }
}
.dashboard-grid {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
@media (min-width: 768px) {
  .dashboard-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .dashboard-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1280px) {
  .dashboard-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.kds-dark {
  --background: #000000;
  --foreground: #ffffff;
  --card: #0a0a0a;
  --border: #262626;
}
.kds-ticket {
  border-radius: var(--radius-lg);
  border-style: var(--tw-border-style);
  border-width: 2px;
  background-color: var(--card);
  padding: calc(var(--spacing) * 4);
}
.kds-ticket-urgent {
  border-color: var(--destructive);
  background-color: var(--destructive);
  @supports (color: color-mix(in lab, red, red)) {
    background-color: color-mix(in oklab, var(--destructive) 20%, transparent);
  }
}
.kds-ticket-warning {
  border-color: var(--color-yellow-500);
  background-color: color-mix(in srgb, oklch(79.5% 0.184 86.047) 20%, transparent);
  @supports (color: color-mix(in lab, red, red)) {
    background-color: color-mix(in oklab, var(--color-yellow-500) 20%, transparent);
  }
}
.kds-ticket-normal {
  border-color: var(--border);
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--muted);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb {
  background: var(--muted-foreground);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--foreground);
}
.focus-visible:focus {
  outline: 2px solid var(--ring);
  outline-offset: 2px;
}
.transition-base {
  transition-property: all;
  transition-timing-function: var(--tw-ease, var(--default-transition-timing-function));
  transition-duration: var(--tw-duration, var(--default-transition-duration));
  --tw-duration: 200ms;
  transition-duration: 200ms;
  --tw-ease: var(--ease-in-out);
  transition-timing-function: var(--ease-in-out);
}
.transition-fast {
  transition-property: all;
  transition-timing-function: var(--tw-ease, var(--default-transition-timing-function));
  transition-duration: var(--tw-duration, var(--default-transition-duration));
  --tw-duration: 150ms;
  transition-duration: 150ms;
  --tw-ease: var(--ease-in-out);
  transition-timing-function: var(--ease-in-out);
}
.transition-slow {
  transition-property: all;
  transition-timing-function: var(--tw-ease, var(--default-transition-timing-function));
  transition-duration: var(--tw-duration, var(--default-transition-duration));
  --tw-duration: 300ms;
  transition-duration: 300ms;
  --tw-ease: var(--ease-in-out);
  transition-timing-function: var(--ease-in-out);
}
.skeleton {
  animation: var(--animate-pulse);
  border-radius: 0.25rem;
  background-color: var(--muted);
}
.status-online {
  height: calc(var(--spacing) * 2);
  width: calc(var(--spacing) * 2);
  border-radius: calc(infinity * 1px);
  background-color: var(--color-green-500);
}
.status-busy {
  height: calc(var(--spacing) * 2);
  width: calc(var(--spacing) * 2);
  border-radius: calc(infinity * 1px);
  background-color: var(--color-yellow-500);
}
.status-offline {
  height: calc(var(--spacing) * 2);
  width: calc(var(--spacing) * 2);
  border-radius: calc(infinity * 1px);
  background-color: var(--color-gray-400);
}
.kds-dark {
  --kds-bg-primary: #000000;
  --kds-bg-secondary: #0a0a0a;
  --kds-bg-tertiary: #111111;
  --kds-text-primary: #ffffff;
  --kds-text-secondary: #e5e5e5;
  --kds-text-muted: #a3a3a3;
  --kds-text-accent: #60a5fa;
  --kds-border-primary: #374151;
  --kds-border-secondary: #4b5563;
  --kds-border-accent: #3b82f6;
  --kds-success: #22c55e;
  --kds-warning: #fbbf24;
  --kds-error: #ef4444;
  --kds-urgent: #dc2626;
  --kds-fire: #b91c1c;
  --kds-grill: #dc2626;
  --kds-fryer: #d97706;
  --kds-salad: #059669;
  --kds-expo: #7c3aed;
  --kds-pantry: #0369a1;
  --kds-dessert: #be185d;
  --kds-beverage: #0891b2;
}
.kds-text-kitchen {
  font-size: 1.125rem;
  line-height: 1.4;
  font-weight: 500;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.8);
  letter-spacing: 0.025em;
}
.kds-text-kitchen-large {
  font-size: 1.5rem;
  line-height: 1.3;
  font-weight: 600;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.9);
  letter-spacing: 0.025em;
}
.kds-text-kitchen-xlarge {
  font-size: 2rem;
  line-height: 1.2;
  font-weight: 700;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.9);
  letter-spacing: 0.05em;
}
.kds-text-high-contrast {
  color: #ffffff;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.9);
  font-weight: 600;
}
.kds-button {
  min-height: 3.75rem;
  min-width: 3.75rem;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  font-weight: 600;
  border-radius: 0.5rem;
  transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  touch-action: manipulation;
  position: relative;
  overflow: hidden;
}
.kds-button:active {
  transform: scale(0.98);
}
.kds-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none !important;
}
.kds-button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}
.kds-button:active::before {
  width: 300px;
  height: 300px;
}
@media (max-width: 768px) {
  .kds-button {
    min-height: 5rem;
    min-width: 5rem;
    font-size: 1.125rem;
  }
}
@media (min-width: 1920px) {
  .kds-button {
    min-height: 5rem;
    font-size: 1.25rem;
  }
  .kds-text-kitchen {
    font-size: 1.25rem;
  }
  .kds-text-kitchen-large {
    font-size: 2rem;
  }
  .kds-text-kitchen-xlarge {
    font-size: 2.5rem;
  }
}
@keyframes kds-urgent-pulse {
  0%, 100% {
    border-color: var(--kds-urgent);
    background-color: rgba(220, 38, 38, 0.1);
    box-shadow: 0 0 0 0 rgba(220, 38, 38, 0.7);
  }
  50% {
    border-color: #f87171;
    background-color: rgba(248, 113, 113, 0.2);
    box-shadow: 0 0 0 10px rgba(220, 38, 38, 0);
  }
}
.kds-urgent {
  animation: kds-urgent-pulse 2s infinite;
}
@keyframes kds-warning-glow {
  0%, 100% {
    border-color: var(--kds-warning);
    box-shadow: 0 0 5px rgba(251, 191, 36, 0.3);
  }
  50% {
    border-color: #fde047;
    box-shadow: 0 0 15px rgba(253, 224, 71, 0.5);
  }
}
.kds-warning {
  animation: kds-warning-glow 3s infinite;
}
@keyframes kds-fire-flash {
  0%, 50%, 100% {
    background-color: rgba(185, 28, 28, 0.3);
    border-color: var(--kds-fire);
  }
  25%, 75% {
    background-color: rgba(239, 68, 68, 0.4);
    border-color: #ef4444;
  }
}
.kds-fire {
  animation: kds-fire-flash 1s infinite;
}
.kds-ticket-normal {
  border-color: var(--kds-border-primary);
  background-color: var(--kds-bg-tertiary);
}
.kds-ticket-warning {
  border-color: var(--kds-warning);
  background-color: rgba(251, 191, 36, 0.1);
}
.kds-ticket-urgent {
  border-color: var(--kds-error);
  background-color: rgba(239, 68, 68, 0.1);
}
.kds-ticket-grid {
  display: grid;
  gap: 1rem;
  padding: 1rem;
  height: 100%;
  overflow-y: auto;
  scroll-behavior: smooth;
}
.kds-ticket-grid-1 {
  grid-template-columns: 1fr;
}
.kds-ticket-grid-2 {
  grid-template-columns: repeat(2, 1fr);
}
.kds-ticket-grid-3 {
  grid-template-columns: repeat(3, 1fr);
}
.kds-ticket-grid-4 {
  grid-template-columns: repeat(4, 1fr);
}
.kds-ticket-grid-5 {
  grid-template-columns: repeat(5, 1fr);
}
.kds-ticket-grid-6 {
  grid-template-columns: repeat(6, 1fr);
}
.kds-auto-scroll {
  scroll-snap-type: y mandatory;
}
.kds-ticket {
  scroll-snap-align: start;
  scroll-margin-top: 1rem;
}
.kds-station-grill {
  border-left: 4px solid var(--kds-grill);
}
.kds-station-fryer {
  border-left: 4px solid var(--kds-fryer);
}
.kds-station-salad {
  border-left: 4px solid var(--kds-salad);
}
.kds-station-expo {
  border-left: 4px solid var(--kds-expo);
}
.kds-station-pantry {
  border-left: 4px solid var(--kds-pantry);
}
.kds-station-dessert {
  border-left: 4px solid var(--kds-dessert);
}
.kds-station-beverage {
  border-left: 4px solid var(--kds-beverage);
}
.kds-timer {
  font-family: 'SF Mono', 'Monaco', 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace;
  font-variant-numeric: tabular-nums;
}
.kds-timer-urgent {
  color: var(--kds-urgent);
  animation: kds-timer-blink 1s infinite;
}
@keyframes kds-timer-blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0.3;
  }
}
.kds-loading {
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  background-size: 200% 100%;
  animation: kds-loading-shimmer 1.5s infinite;
}
@keyframes kds-loading-shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
.kds-dark *:focus-visible {
  outline: 2px solid var(--kds-border-accent);
  outline-offset: 2px;
  border-radius: 0.25rem;
}
.kds-dark ::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}
.kds-dark ::-webkit-scrollbar-track {
  background: var(--kds-bg-secondary);
  border-radius: 6px;
}
.kds-dark ::-webkit-scrollbar-thumb {
  background: var(--kds-border-secondary);
  border-radius: 6px;
  border: 2px solid var(--kds-bg-secondary);
}
.kds-dark ::-webkit-scrollbar-thumb:hover {
  background: #6b7280;
}
.kds-alert-error {
  background: linear-gradient(135deg, rgba(185, 28, 28, 0.2), rgba(239, 68, 68, 0.1));
  border: 1px solid var(--kds-error);
  box-shadow: 0 4px 12px rgba(220, 38, 38, 0.3);
}
.kds-alert-warning {
  background: linear-gradient(135deg, rgba(217, 119, 6, 0.2), rgba(251, 191, 36, 0.1));
  border: 1px solid var(--kds-warning);
  box-shadow: 0 4px 12px rgba(245, 158, 11, 0.3);
}
.kds-alert-info {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.2), rgba(96, 165, 250, 0.1));
  border: 1px solid var(--kds-border-accent);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}
.kds-performance-mode {
  will-change: transform, opacity;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
@media (prefers-reduced-motion: reduce) {
  .kds-urgent,
  .kds-warning,
  .kds-fire,
  .kds-timer-urgent,
  .kds-loading {
    animation: none !important;
  }
  .kds-button::before {
    display: none;
  }
}
@media print {
  .kds-no-print {
    display: none !important;
  }
  .kds-ticket {
    background: white !important;
    color: black !important;
    border: 2px solid black !important;
    page-break-inside: avoid;
    margin-bottom: 20px;
    box-shadow: none !important;
  }
  .kds-text-kitchen,
  .kds-text-kitchen-large,
  .kds-text-kitchen-xlarge {
    text-shadow: none !important;
    color: black !important;
  }
  .kds-button {
    display: none !important;
  }
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
